<template>
  <div class="addzhanghu">
    <!--头部-->
    <!-- <header class="aui-bar aui-bar-nav" id="aui-header">
      <a class="aui-btn aui-pull-left" tapmode="" onclick="history.go(-1)">
        <span class="aui-iconfont aui-icon-left"></span>
      </a>
      <div class="aui-title aui-font-size-12">账号列表</div>
    </header> -->
		<Header title="账号列表">
			<a class="aui-btn aui-pull-left" tapmode="" onclick="history.go(-1)" slot="left">
			  <span class="aui-iconfont aui-icon-left"></span>
			</a>
			<a class="aui-btn aui-pull-right" slot="right">
			  <router-link to="/addzhanghu">
			
			    <span class="aui-iconfont aui-icon-plus"></span>
			  </router-link>
			</a>
		</Header>
    <!--内容区-->
	<ul class="aui-list aui-media-list" style="background: #f2f2f2;margin-bottom: 100px;">
	  <li class="aui-list-item  z-margin-b1" style="background: white" v-for="item in accountList">
	      <div class="aui-media-list-item-inner">
	        <div class="aui-list-item-inner">
	          <div class="aui-list-item-title">
	            <span class="aui-pull-left aui-font-size-14 z-padding-r2">收款类型：{{item.accountType}}</span>
	          </div>
	        </div>
	      </div>
	      <div class="aui-info z-edit-padding">
	        <div class="aui-info-item aui-font-size-12">
	          <span class="" style="color: black">{{item.accountNameReal}}</span>
			  <span class="" style="color: #ccc;">{{item.accountMobile}}</span>
	        </div>
	      </div>
	      <div class="aui-grid  z-padding-b-5 z-addressList-bg">
	        <div class="aui-row">
	          <div class="aui-col-xs-4">
				<div class="aui-grid-label aui-font-size-12 aui-border-r" @click.stop="reviseAccount(item)">
					<span class="iconfont">&#xe6b8;</span>修改
				</div>
	          </div>
	          <div class="aui-col-xs-4">
	            <div class="aui-grid-label aui-font-size-12 aui-border-r" @click.stop="deleteAccount(item.accountId)">
	              <span class="iconfont">&#xe605;</span> 删除
	            </div>
	          </div>
	        </div>
	      </div>
	
	    </li>
	  
	</ul>
    
		

    

  </div>
</template>

<script>
	import Header from '../../../components/HeaderTop/HeaderTop.vue'
	export default {
		name: "addzhanghu",
		data(){
			return{
				accountList:[]
				
			}
		},
		components:{
			Header
		},
		created(){
			this.$api.getAccountList().then(res=>{
				this.accountList = res.data.data
			})
		},
		methods:{
			deleteAccount(id){
				this.$api.deleteAddress({
					'accountId':id
				}).then(res=>{
					if(res.data.message=='执行成功'){
						this.accountList = this.accountList.filter(item=>{
							
							return item.accountId!=id
						})
						
					}else{
						console.log('删除失败')
					}
				})
			},
			reviseAccount(item){
				this.$router.push({path:'/reviseAccount/'+JSON.stringify(item)})
			},
		}
	}
</script>

<style scoped>
.addzhanghu{
	padding-top: 2.25rem;
}
</style>



