<template>
    <div class="tixianjilu" ref="loadBox">
      <!--头部-->
      <!-- <header class="aui-bar aui-bar-nav" id="aui-header">
        <a class="aui-btn aui-pull-left" tapmode="" onclick="history.go(-1)">
          <span class="aui-iconfont aui-icon-left"></span>
        </a>
        <div class="aui-title aui-font-size-12">提现记录</div>
      </header> -->
		<Header title="提现记录">
			<a class="aui-btn aui-pull-left" tapmode="" @click="$router.go(-1)" slot="left">
			  <span class="aui-iconfont aui-icon-left"></span>
			</a>
		</Header>
      <!--记录内容-->
		<ul class="aui-list aui-media-list">
				<li class="aui-list-item" v-for="item in cashRecord">
					<div class="aui-media-list-item-inner">
						<div class="aui-list-item-inner">
							<div class="aui-list-item-title aui-font-size-18">
								<span class="aui-pull-left aui-font-size-14">微信支付</span>
								<span class="aui-pull-right aui-font-size-12 colorOrage">￥{{item.cashScore}}</span>
							</div>

						</div>
					</div>
					<div class="aui-info" style="margin-top: -14px;">
						<div class="aui-info-item aui-font-size-12">
							<span class="aui-margin-l-5">账号:{{item.cashAccountId}}</span>

						</div>

						<div class="aui-info-item aui-font-size-12">{{item.cashState==0?'已通过':'审核中'}}</div>
						
					</div>
					<p class="aui-margin-l-5 z-padding-b-5 aui-font-size-12">时间:{{item.cashDate}}</p>
				</li>
		</ul>
		<div v-if="isbottom == -1" class="isbottom aui-font-size-12">
				<i class="iconfont icon-jiazai1"></i>
				<span>加载中，请稍后</span>
		</div>
		<div v-if="isbottom == 1" class="isbottom aui-font-size-12">
				<span>没有更多数据了</span>
		</div>
    </div>
</template>

<script>
	import Header from '../../../components/HeaderTop/HeaderTop.vue'
    export default {
        name: "tixianjilu",
		data(){
			return{
				cashRecord:{},
				page:1,
				pages:1,
				pageSize:10,
				isbottom:0
			}
		},
		components: {
			Header,
		},
		created(){
			this.$api.CashRecord({
				'pageNum':this.page,
				'pageSize':this.pageSize
			}).then(res=>{
				this.cashRecord = res.data.list
				this.pages = res.data.pages
			})
				
		},
		mounted(){
			this.$nextTick(() => {
				window.addEventListener('scroll', this.handleScroll)
			})
		},
		methods: {
			handleScroll(){
				let $el = this.$refs.loadBox
				console.log($el.clientHeight,document.documentElement.scrollTop,document.body.offsetHeight)
				if($el.clientHeight-document.documentElement.scrollTop-document.body.offsetHeight<=0){
					this.onInfinite()
				}
			},
			onInfinite(){
				let vm = this;
				if(this.page < this.pages){
					this.isbottom = -1
					this.page++
					this.$api.CashRecord({
						'pageNum':this.page,
						'pageSize':this.pageSize
					}).then(res=>{
						this.cashRecord = this.cashRecord.concat(res.data.list)
						this.isbottom = 0
					})
				}else{
					this.isbottom = 1
				}
			}
		},
		beforeRouteLeave(to,from,next){
			removeEventListener('scroll',this.handleScroll,false)
			next()
		}
    }
</script>

<style scoped>
.tixianjilu{
	padding-top:2.25rem ;
}
.isbottom{
	text-align: center;
	padding: 5px 0;
}
</style>



