<template>
    <div class="mytuanyou" ref="loadBox">
      <!--头部-->
      <!-- <header class="aui-bar aui-bar-nav" id="aui-header">
        <a class="aui-btn aui-pull-left" tapmode="" @click="prev">
          <span class="aui-iconfont aui-icon-left"></span>
        </a>
        <div class="aui-title aui-font-size-12">我的团友</div>
      </header> -->
	  <Header title="我的团友">
		  <a class="aui-btn aui-pull-left" tapmode="" @click="prev" slot="left">
		    <span class="aui-iconfont aui-icon-left"></span>
		  </a>
	  </Header>
      <!--我的团友人数-->
      <div class="aui-list aui-form-list" v-cloak>
        <div class="aui-list-header">我的团友共计:{{friendNum}}人</div>

      </div>
      <!--我的团友信息-->
      <div class="aui-card-list z-margin-b0 z-border-b1" v-for="item in friendList">
        <div class="userinfo-header">
          <div class="aui-info">
            <div class="aui-info-item z-margin-l5">
              <img :src="'http://'+item.userWxImg" style="width:2.2rem">
              <span class="aui-margin-l-10 aui-margin-r-10">{{item.userWxName}}</span>
            </div>
            <div class="aui-info-item z-tuan-margin-r8 aui-font-size-12 colorBlack">注册时间：{{item.userDate}}</div>
          </div>
        </div>
        <!--<div class="aui-card-list-content-padded" style="margin-top: -15px;margin-left: 40px;">注册时间：2019-01-22</div>-->
      </div>
	  <div v-if="isbottom == -1" class="isbottom aui-font-size-12">
	  		<i class="iconfont icon-jiazai1"></i>
	  		<span>加载中，请稍后</span>
	  </div>
	  <div v-if="isbottom == 1" class="isbottom aui-font-size-12">
	  		<span>没有更多数据了</span>
	  </div>
    </div>
</template>

<script>
	import Header from '../../../components/HeaderTop/HeaderTop.vue'
	export default {
		name: "mytuanyou",
		data(){
			return{
				friendNum:0,
				friendList:[],
				page:1,
				pageSize:10,
				isbottom:0,
				pages:1
			}
		},
		components:{
			Header
		},
		created(){
			
			this.$api.getFriend({
				'pageNum':this.page,
				'pageSize':this.pageSize
			}).then(res=>{
				this.friendList = res.data.list
				this.friendNum = res.data.total
				this.pages = res.data.pages
			})
		},
		mounted(){
			this.$nextTick(() => {
				window.addEventListener('scroll', this.handleScroll)
			})
		},
		methods:{
			prev(){
				this.$router.go(-1);
			},
			handleScroll(){
				let $el = this.$refs.loadBox
				console.log($el.clientHeight,document.documentElement.scrollTop,document.body.offsetHeight)
				if($el.clientHeight-document.documentElement.scrollTop-document.body.offsetHeight<=0){
					this.onInfinite()
				}
			},
			onInfinite(){
				let vm = this;
				if(this.page < this.pages){
					this.isbottom = -1
					this.page++
					this.$api.getFriend({
						'pageNum':this.page,
						'pageSize':this.pageSize
					}).then(res=>{
						this.friendList = this.friendList.concat(res.data.list)
						this.isbottom = 0
						
					})
					
				}else{
					this.isbottom = 1
				}
			}
		},
		beforeRouteLeave(to,from,next){
			removeEventListener('scroll',this.handleScroll,false)
			next()
		}
	}
</script>

<style scoped>
	.mytuanyou{
		padding-top: 2.25rem;
	}
	[v-cloak] {
	  display: none;
	}
	.isbottom{
		text-align: center;
		padding: 5px 0;
	}
</style>



