<template>
  <!--<div class="orderfirm">-->
    <div class="bg-content orderfirm">
      <div class="aui-list aui-media-list">
        <!--头部-->
        <!-- <header class="aui-bar aui-bar-nav" id="aui-header">
          <a class="aui-btn aui-pull-left" tapmode="" onclick="history.go(-1)">
            <span class="aui-iconfont aui-icon-left"></span>
          </a>
          <div class="aui-title aui-font-size-12">确认订单</div>
        </header> -->
				<Header title="确认订单">
					<a class="aui-btn aui-pull-left" tapmode="" onclick="history.go(-1)" slot="left">
					  <span class="aui-iconfont aui-icon-left"></span>
					</a>
					
				</Header>

      </div>

      <!--收货地址-->
      <div class="aui-list aui-list-in z-background-image">
        <div class="aui-list-item z-background-image">
					<router-link :to="'/addressList?goodId='+$route.params.id" class="aui-list-item-arrow">
						<div class="aui-list-item-inner" v-if="Object.keys(addressDetail).length==0">
							<div class="aui-list-item-title aui-font-size-12">新增收货地址</div>
						</div>
						<div class="aui-list-item-inner" v-else style="flex-direction: column;align-items: flex-start;justify-content: center;">
							<div class="aui-list-item-title aui-font-size-12">
								<span>{{addressDetail.addressName}}</span>
								<span style="color: #aaa;">{{addressDetail.addressPhone}}</span>
							</div>
							<div class="aui-list-item-title aui-font-size-12">
								<span>{{addressDetail.addressAddress}}</span>
							</div>
						</div>
					</router-link>
        </div>

      </div>
      <!--商品内容-->
      <div class="z-margin-b100">
        <div class="z-margin-t5">
          <!--主要是遍历购买多数商品-->
          <div class="aui-card-list aui-border-t aui-border-b   z-margin-b1 z-shop">
            <div class="aui-card-list-header aui-card-list-user">
              <div class="aui-card-list-user-avatar">
               <img :src="'http://'+imgUrl"/>
              </div>
              <div class="aui-card-list-user-name">
                <div class="aui-font-size-12 aui-ellipsis-2" style="width: 95%;">{{orderDetail.goodsTitle}}</div>

              </div>
            </div>
            <div class="aui-card-list-content aui-border-t z-background-image" style="margin-left: 3.5rem;">
              <div class="aui-list aui-list-noborder" style="background-color: transparent;">
                <div class="aui-list-item aui-padded-l-0">
                  <div class="aui-list-item-inner">
                    <div class="aui-list-item-title aui-font-size-14 goods-title">
                      <span class="z-colorshenhong">积分</span>
                      <span class="z-colorshenhong aui-font-size-16">{{orderDetail.goodsCurrnetScore}}</span>
                    </div>
                    <div class="aui-list-item-right goods-price z-margin-r1 z-colorshenhong">x{{orderNum}}</div>
                  </div>
                </div>
              </div>

            </div>
            <!--购买数量-->
            <div class="aui-list aui-list-in z-padding-t1 z-padding-b1">
              <div class="aui-list-item z-background-image">

                <div class="aui-list-item-inner z-background-image">
                  <div class="aui-list-item-title aui-font-size-12">购买数量</div>
                  <div class="aui-list-item-right">

                    <div class="x-order-count" style="">
                      <input class="min" name="" type="button" value="-" style="width:50px;" @click="orderNum<=0?orderNum=0:orderNum--"/>
                      <input class="text_box" name="goodnum" type="text" :value="orderNum" style=" height: 23px;width:50px; border: 1px solid #dddddd;text-align: center;" />
                      <input class="add" name="" type="button" value="+" style="width:50px;" @click="orderNum++"/>
                    </div>
                  </div>
                </div>
              </div>

            </div>





          </div>


        </div>

        <!--共计4件商品：合计454积分-->
        <div class="aui-list aui-list-in z-background-image">

          <div class="aui-list-item z-background-image">

            <div class="aui-list-item-inner">
              <div class="aui-list-item-title"></div>
              <div class="aui-list-item-right colorBlack" style="max-width: 60%;">
                共计 <span class="fontcolor aui-font-size-14 z-colorshenhong">{{orderNum}}</span> 件商品
                合计:<span class="fontcolor aui-font-size-14 z-colorshenhong">{{getPointSum}}积分</span>
              </div>
            </div>
          </div>
        </div>

        <!--输入买家联系方式-->
        <div class="aui-content  z-margin-t5">
          <div class="aui-list aui-form-list z-background-image">
            <div class="aui-list-item">
              <div class="aui-list-item-inner">
                <div class="aui-list-item-label aui-font-size-12">
                  手机号：
                </div>
                <div class="aui-list-item-input aui-font-size-12" style=" margin-left: -32px;">
                  <input class="aui-font-size-12" type="number" placeholder="请输入联系人手机号">
                </div>
              </div>
            </div>

          </div>
        </div>

        <!--输入买家联系人-->
        <div class="aui-content aui-margin-b-15">
          <div class="aui-list aui-form-list z-background-image">
            <div class="aui-list-item z-background-image">
              <div class="aui-list-item-inner">
                <div class="aui-list-item-label aui-font-size-12">
                  联系人：
                </div>
                <div class="aui-list-item-input aui-font-size-12" style=" margin-left: -32px;">
                  <input class="aui-font-size-12" type="text" placeholder="请输入联系人姓名">
                </div>
              </div>
            </div>

          </div>
        </div>

        <!--买家留言-->
        <div class="aui-content  z-margin-t5">
          <div class="aui-list aui-form-list z-background-image">
            <div class="aui-list-item">
              <div class="aui-list-item-inner">
                <div class="aui-list-item-label aui-font-size-12">
                  留言:
                </div>
                <div class="aui-list-item-input aui-font-size-12" style=" margin-left: -32px;">
                  <input class="aui-font-size-12" type="text" placeholder="请输入50个字以内" v-model="message">
                </div>
              </div>
            </div>

          </div>
        </div>



        <!--运费-->
        <div class="aui-list aui-list-in z-background-image">
          <div class="aui-list-item">

            <div class="aui-list-item-inner">
              <div class="aui-list-item-title aui-font-size-12">运费</div>
              <div class="aui-list-item-right">包邮</div>
            </div>
          </div>

        </div>

        <!--选择积分支付方式-->
        <!--<div class="aui-list aui-list-in z-background-image z-margin-t1">-->
          <!--<div class="aui-list-item z-background-image" style="position: relative">-->

            <!--<div class="aui-list-item-inner">-->
              <!--<div class="aui-list-item-title aui-font-size-12">使用积分支付</div>-->
              <!--<div class="aui-list-item-right">-->
                <!--<div class="aui-content-padded fontcolor" style="position: absolute;top: -25px;right: 0px;">-->
                  <!--<label><input class="aui-radio" type="radio" name="demo" checked=""></label>-->

                <!--</div>-->
              <!--</div>-->
            <!--</div>-->
          <!--</div>-->

        <!--</div>-->
        <!--积分使用说明-->
        <div class="aui-list aui-list-in z-background-image">
          <div class="aui-list-item z-background-image">

            <div class="aui-list-item-inner">
              <div class="aui-list-item-title aui-font-size-12 z-padding-t1">
                <p>通用积分：<span class="z-colorshenhong fl">0.00</span></p>
                <p class="z-colorshenhong z-padding-t1 z-padding-b-5">商品价格大于积分余额自动跳转支付宝支付不足余额</p>
              </div>
              <div class="aui-list-item-right"></div>
            </div>
          </div>

        </div>


      </div>

      <!--底部-->
      <footer class="aui-bar aui-bar-tab aui-margin-t-15" style="bottom: 0;">
        <div class="aui-bar-tab-item z-colorshenhong" tapmode="" style="width:10rem;">
          <span class="aui-font-size-14">实付款：</span>
          <span class="fontcolor aui-font-size-16">{{getPointSum}} 积分</span>

        </div>

        <div class="aui-bar-tab-item z-backgroundshenju" tapmode=""  style="width: auto;">

          <div class="aui-bar-tab-label colorWhite" @click="submitOrder">提交订单</div>
        </div>


      </footer>


    </div>


    
</template>

<script>
	import Axios from 'axios'
	import Header from '../../../components/HeaderTop/HeaderTop.vue'
    export default {
        name: "orderfirm",
				data(){
					return{
						orderDetail:{},
						imgUrl:'',
						orderNum:1,
						addressDetail:{},
						message:'',//留言
					}
				},
				components: {
					Header
				},
				computed: {
					getPointSum(){
						return this.orderNum * this.orderDetail.goodsCurrnetScore
					}
				},
				created(){
					this.$api.orderDetail({
						'goodsId':this.$route.params.id
					}).then(res=>{
						this.orderDetail = res.data
						this.imgUrl = res.data.goodsImgList[0].goodsImgImg
					})
					
					if(this.$route.query.adress){
						this.addressDetail = JSON.parse(this.$route.query.adress)
					}else{
						this.$api.orderAddress().then(res=>{
							this.addressDetail = res.data.address
						})
					}
				},
				methods: {
					//提交订单
					submitOrder(){
						console.log(this.addressDetail)
						this.$api.submitOrder({
							'orderMessage':this.message,
							'orderTotalPrice':this.getPointSum,
							'orderAddressId':this.addressDetail.addressId
						}).then(res=>{
							console.log(res)
						})
					}
				},
    }
</script>

<style scoped>
.orderfirm{
	padding-top: 2.25rem;
}
[v-cloak] {
  display: none;
}
</style>



