<template>
    <div class="HeaderTop">
      <!--头部区域选择-->
      <header class="aui-bar aui-bar-nav" id="aui-header">

        <slot name="left"></slot>
        <div class="aui-title aui-font-size-12">{{title}}</div>
        <slot name="right"></slot>
      </header>
    </div>
</template>

<script>
    export default {
        name: "HeaderTop",
      //接收传过来的参数
      props:{
          title:String


      },
    }
</script>

<style scoped>
  /*头部导航条*/
  .aui-bar-nav {
    background: #fd5a41;
		position: fixed !important;
		width: 100%;
		top: 0;
  }

</style>



