<template>

  <div class="addressList">
    <!-- <header class="aui-bar aui-bar-nav" id="aui-header">
      <a class="aui-btn aui-pull-left" tapmode="" onclick="history.go(-1)">
        <span class="aui-iconfont aui-icon-left"></span>
      </a>
      <div class="aui-title aui-font-size-12">地址列表</div>
      <a class="aui-btn aui-pull-right">
        <router-link to="/addAdress">

          <span class="aui-iconfont aui-icon-plus"></span>
        </router-link>
      </a>
    </header> -->
		<Header title="地址列表">
			<a class="aui-btn aui-pull-left" tapmode="" onclick="history.go(-1)" slot="left">
			  <span class="aui-iconfont aui-icon-left"></span>
			</a>
			<a class="aui-btn aui-pull-right" slot="right">
			  <router-link to="/addAdress">
			
			    <span class="aui-iconfont aui-icon-plus"></span>
			  </router-link>
			</a>
		</Header>
    <!--内容区-->
    <ul class="aui-list aui-media-list" style="background: #f2f2f2;margin-bottom: 100px;">
      <li class="aui-list-item  z-margin-b1" style="background: white" v-for="item in addressList" @click="selectAddress(item)">
          <!--路由跳转到确认订单中-->

          <div class="aui-media-list-item-inner">
            <div class="aui-list-item-inner">
              <div class="aui-list-item-title">
                <span class="aui-pull-left aui-font-size-14 z-padding-r2">{{item.addressName}}</span>
                <span class="aui-font-size-16 z-color_hei">{{item.addressPhone}}</span>
              </div>
            </div>
          </div>
          <div class="aui-info z-edit-padding">
            <div class="aui-info-item aui-font-size-12">
							<span class="default" v-if="item.addressDefaultState=='0'">默认&nbsp;</span>
              <span class="" style="color: black">{{item.addressAddress}}</span>
            </div>
          </div>


          <div class="aui-grid  z-padding-b-5 z-addressList-bg">
            <div class="aui-row">
              <div class="aui-col-xs-4">
                <div class="aui-grid-label aui-font-size-12 aui-border-r" @click.stop="setDefault(item.addressId)">
                  <span class="iconfont">&#xebbb;</span>设为默认
                </div>
              </div>
              <div class="aui-col-xs-4">
								<div class="aui-grid-label aui-font-size-12 aui-border-r" @click.stop="reviseAddress(item)">
									<span class="iconfont">&#xe6b8;</span>修改
								</div>
              </div>
              <div class="aui-col-xs-4">
                <div class="aui-grid-label aui-font-size-12 aui-border-r" @click.stop="delectAdress(item.addressId)">
                  <span class="iconfont">&#xe605;</span> 删除
                </div>
              </div>
            </div>
          </div>

        </li>
      
    </ul>
  </div>
    
</template>

<script>
	import Header from '../../../components/HeaderTop/HeaderTop.vue'
    export default {
        name: "addressList",
				data(){
					
					return{
						addressList:[]
					}
				},
				components: {
					Header
				},
				created(){
					
					this.$api.getAddressList().then(res=>{
						this.addressList = res.data
					})
				},
				methods: {
					delectAdress(adressId){
						
						this.$api.deleteAddress({
							'addressId':adressId
						}).then(res=>{
							if(res.data.message=='执行成功'){
								this.addressList = this.addressList.filter(item=>{
									
									return item.addressId!=adressId
								})
								
							}else{
								console.log('删除失败')
							}
						})
					},
					reviseAddress(item){
						this.$router.push({path:'/reviseAddress/'+JSON.stringify(item)})
					},
					setDefault(addressId){
						
						this.$api.setDefaultAddress({
							'addressId':addressId
						}).then(res=>{
							if(res.data.message=='执行成功'){
								this.addressList.map(item=>{
									if(item.addressId == addressId){
										item.addressDefaultState = 0
									}else{
										item.addressDefaultState = 1
									}
									return item
								})
								
							}else{
								console.log('执行失败')
							}
						})
					},
					selectAddress(address){
						this.$router.push({path:'/orderfirm/'+this.$route.query.goodId,query:{adress:JSON.stringify(address)}})
					}
				},
    }
</script>

<style scoped>
.addressList{
	padding-top: 2.25rem;
}
.aui-info-item .default{
	color: #fd5a41;
	background: #d8b3ad;
	padding: 0 3px;
}
</style>



