<template>
    <div class="jifenduihuan">
      <!--头部-->
      <!-- <header class="aui-bar aui-bar-nav z-bar-nav" id="aui-header">
        <a href="javascript:" class="aui-btn aui-pull-left" @click="$router.back(-1)">
          <span class="aui-iconfont aui-icon-left"></span>
        </a>
        <div class="aui-title aui-font-size-12">积分提现</div>
      </header> -->
			<Header title="积分提现">
				<a href="javascript:" class="aui-btn aui-pull-left" @click="$router.back(-1)" slot="left">
				  <span class="aui-iconfont aui-icon-left"></span>
				</a>
			</Header>
      <!--内容区-->
      <div class="aui-list-item aui-list-item-middle"
           style="background-image: url('../../../../static/images/center_bg.png');background-size: cover;height: 150px;position: relative;">
        <div class="aui-card-list-content aui-text-center z-padding-t3">
          <div class="aui-font-size-12 colorWhite">积分金额(元)</div>
          <p class="aui-margin-b-10 z-font-size28  colorWhite aui-font-size-12">{{scoreDetail.userCurrentScore}}</p>

        </div>
      </div>
      <!--积分金额与已提现-->
      <div class="aui-row" style="background: white;padding: 5px 0px;">
        <div class="aui-col-xs-5 aui-border-r aui-text-center">
            <small class="aui-text-warning">{{scoreDetail.userTotalScore}}</small>
          <div class="aui-gird-lable aui-font-size-12">积分总额</div>
        </div>
        <div class="aui-col-xs-5 aui-border-r aui-text-center">
            <small class="aui-text-danger"> {{scoreDetail.userConsumeScore}}</small>
          <div class="aui-gird-lable aui-font-size-12">已提现</div>
        </div>
      </div>
      <!--提现账号管理-->
      <ul class="aui-list aui-list-in z-margin-t5 z-background-image">
        <li class="aui-list-item" @click="goTo">
          <span  class="aui-list-item-arrow">
            <div class="aui-list-item-inner">
              <div class="aui-list-item-title aui-font-size-12">提现账号管理</div>
            </div>
          </span>
        </li>
        <li class="aui-list-item" @click="goTo1">

          <div class="aui-list-item-inner">
            <span  class="aui-list-item-arrow">
              <div class="aui-list-item-title aui-font-size-12">提现记录</div>
            </span>
          </div>

        </li>
        <li class="aui-list-item" @click="goTo2">

          <div class="aui-list-item-inner">
            <span  class="aui-list-item-arrow">
              <div class="aui-list-item-title aui-font-size-12">积分明细</div>
            </span>
          </div>

        </li>
      </ul>
        <div class="aui-btn  aui-btn-block aui-btn-sm  btn-bg colorWhite" style="margin-top: 80px;" @click="gotoTX">提现</div>
    </div>

</template>

<script>
	import Header from '../../../components/HeaderTop/HeaderTop.vue'
    export default {
        name: "jifenduihuan",
				data(){
					return{
						scoreDetail:{}
					}
				},
				components: {
					Header
				},
				created(){
					this.$api.withdraw().then(res=>{
						this.scoreDetail = res.data
					})
				},
        methods:{
          prev(){
            this.$router.go(-1);
          },
          goTo(){
            this.$router.push('/accountList');
          },
          goTo1(){
            this.$router.push('/tixianjilu');
          },
          goTo2(){
            this.$router.push('/jifenmingxi');
          },
          gotoTX(){
            this.$router.push('/tixian');
          },
        }
    }
</script>

<style scoped>
  /*头部导航条*/
  .aui-bar-nav {
    background: #fd5a41;
  }
	.jifenduihuan{
		padding-top: 2.25rem;
	}
	
</style>



