<template>
  <div class="Msite" ref="loadBox">
    <!--头部区域-->
    <HeaderTop title="莱田洗碗机">
      <a class="aui-btn aui-pull-left" slot="left" tapmode="" onclick="history.go(-1)">
        <span class="aui-iconfont aui-icon-left"></span>
      </a>
    </HeaderTop>

    <!-- 首页导航 -->
    <nav class="msite_nav border-1px">
      <div class="swiper-container">
        <div class="swiper-wrapper">
          <div class="swiper-slide" v-for="item in bannerUrls">
            <img :src="'http://'+item.imgImgUrl"/>
          </div>
          <!-- <div class="swiper-slide">
            <img src="../../assets/banner3.png"/>
          </div> -->
        </div>
        <!-- Add Pagination -->
        <div class="swiper-pagination"></div>
      </div>
    </nav>


    <!--设备续费 积分提现 当前积分-->
    <div class="aui-content">
      <div class="aui-grid">
        <div class="aui-row" style="display: flex;">
          <router-link to="/profile" class="aui-col-xs-4 z-flex-column" style="padding: 0;">

            <img class="z-width-img" src="../../../static/images/icon_shebeixufei.png" alt="">
            <div class="aui-grid-label aui-font-size-12">设备续费</div>

          </router-link>
          <router-link to="/jifentixian" class="aui-col-xs-4 z-flex-column" style="padding: 0;">
            <img class="z-width-img" src="../../../static/images/icon_jifentixian.png" alt="">
            <div class="aui-grid-label aui-font-size-12">积分提现</div>

          </router-link>
          <div class="aui-col-xs-4 z-flex-column" style="padding: 0;">
            <img class="z-width-img" src="../../../static/images/icon_dangqianjifen.png" alt="">
            <div class="aui-grid-label aui-font-size-12">
              当前积分
              <span class="z-colorju">{{userCurrentScore}}</span>
            </div>
          </div>

        </div>
      </div>
    </div>
    <!--商品代言区-->
    <div class="clearfix bg-background">
      <div class="aui-card-list  z-margin-b0 z-shop2" style="">
        <div class="aui-card-list-content-padded aui-padded-t-5 aui-padded-b-5"
             style="text-align: center;padding-bottom: 50px;background: #fd5a41;color: #fff;">
          我为莱田洗碗机代言
        </div>
        <div class="aui-card-list-content z-card-list-content">
          <div class="aui-row aui-row-padded">
            <div class="aui-col-xs-12">
              <img src="../../../static/images/banner06.png" style="height: 160px;">
            </div>
          </div>
        </div>
      </div>
    </div>
    <!--积分兑换好礼-->
    <ul class="aui-list aui-list-in z-background-image z-margin-t1">
      <li class="aui-list-item z-background-image">

        <div class="aui-list-item-inner">
          <div class="aui-list-item-title aui-font-size-12">积分兑换好礼</div>
          <div class="aui-list-item-right"><span class="z-colorhui aui-font-size-12"> >> 商城</span></div>
        </div>
      </li>

    </ul>

    <!--商品区-->
		
			<div class="clearfix bg-background z-margin-b100">
				<div class="aui-card-list z-shop1 z-margin-b0" v-for="item in goodsList">
					<router-link :to='"/productDetail/"+item.goodsId'>
						<div class="aui-card-list-content z-card-list-content">
							<div class="aui-row aui-row-padded">
								<div class="aui-col-xs-12">
									<img :src="'http://'+item.goodsImgList[0].goodsImgImg">
								</div>
							</div>
						</div>
						<div class="aui-card-list-content-padded aui-padded-t-5 aui-padded-b-5 aui-ellipsis">
							{{item.goodsTitle}}
						</div>
						<div class="aui-card-list-content-padded aui-padded-t-5 aui-padded-b-5 z-colorshenju">
							{{item.goodsCurrnetScore}} 积分
						</div>
					</router-link>
				</div>
				
				
				<div v-if="isbottom == -1" class="isbottom aui-font-size-12">
						<i class="iconfont icon-jiazai1"></i>
						<span>加载中，请稍后</span>
				</div>
				<div v-if="isbottom == 1" class="isbottom aui-font-size-12">
						<span>没有更多数据了</span>
				</div>
			</div>
		
		
		

  </div>
</template>

<script>
  import Swiper from 'swiper'
  import 'swiper/dist/css/swiper.min.css'
  import HeaderTop from '../../components/HeaderTop/HeaderTop'

  export default {
    name: "Msite",
		data(){
			return{
				bannerUrls:[],
				goodsList:[],
				isbottom: 0,
				page: 1,
				pageSize:2,
				el:{},
				pages:1,
				userCurrentScore:0
				
			}
		},
    components: {
      //使用组件化开发
      HeaderTop
    },
		created(){
			this.getGoods()
			
		},
    mounted() {
      //轮播图的实现事件
      new Swiper('.swiper-container', {
        pagination: {
          el: '.swiper-pagination',
        },
        loop: true,
				observer:true,//修改swiper自己或子元素时，自动初始化swiper
				observeParents:true//修改swiper的父元素时，自动初始化swiper
      })
			this.$nextTick(() => {
				window.addEventListener('scroll', this.handleScroll)
			})
			
    },
		methods:{
			getGoods(){
				this.$api.getIndex({
					'pageNum':this.page,
					'pageSize':this.pageSize
				}).then(res=>{
					console.log(res)
					this.bannerUrls = res.data.imgList
					this.goodsList = this.goodsList.concat(res.data.goodsList.list)
					this.pages = res.data.goodsList.pages
					this.userCurrentScore = res.data.userCurrentScore
				})
			},
			handleScroll(){
				let $el = this.$refs.loadBox
				console.log($el.clientHeight,document.documentElement.scrollTop,document.body.offsetHeight)
				if($el.clientHeight-document.documentElement.scrollTop-document.body.offsetHeight+100<=0){
					this.onInfinite()
				}
			},
			onInfinite(){
				console.log(this.page,this.pages)
				if(this.page < this.pages){
					this.isbottom = -1
					this.page++
					this.$api.getIndex({
						'pageNum':this.page,
						'pageSize':this.pageSize
					}).then(res=>{
						this.goodsList = this.goodsList.concat(res.data.goodsList.list)
						this.isbottom = 0
					})
					
				}else{
					this.isbottom = 1
				}
				
			},
			
		},
		beforeRouteLeave(to,from,next){
			removeEventListener('scroll',this.handleScroll,false)
			next()
		}
		
		
  }
</script>

<style scoped>
.Msite{
	padding-top: 2.25rem;
}

  .aui-bar-tab .aui-active {
    color: #fd5a41;

  }

  .z-card-list-content img {
    width: 98% !important;
    display: block;
		height: 170px;
  }

  .z-flex-column {
    display: flex !important;
    flex-direction: column;
    align-items: center;
  }

  .z-shop1 {
    width: 48%;
    float: left;
    margin-left: 3px;
    margin-right: 3px;
    /*border-radius: 5px;*/
    border: 1px solid #ebebeb;
		margin-top: 3px;
  }

  .z-shop2 {
    width: 100%;
    margin: 0px auto;

  }

  .aui-row-padded [class*=aui-col-xs-] {
    padding: 0;
  }

  /*轮播图样式*/

  .msite_nav {
    /*margin-top: 45px;*/
    /* height: 200px; */
    background: #fff;
  }

  .msite_nav .swiper-container {
    width: 100%;
    height: 100%;
  }

  .msite_nav .swiper-container .swiper-slide {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-wrap: wrap;
  }
	

  .msite_nav .swiper-container .swiper-slide .link_to_food {
    width: 25%;
  }

  .food_container {
    display: block;
    width: 100%;
    text-align: center;
    padding-bottom: 10px;
    font-size: 0;
  }

  .food_container img {
    display: inline-block;
    width: 50px;
    height: 50px;
  }

  .swiper-slide .link_to_food span {
    display: block;
    width: 100%;
    text-align: center;
    font-size: 13px;
    color: #666
  }

  .swiper-pagination > span.swiper-pagination-bullet-active {
    background: #02a774
  }
	.isbottom{
		text-align: center;
		padding: 5px 0;
	}

</style>



