<template>
    <div class="myshebei">
      <!--头部-->
      <header class="aui-bar aui-bar-nav" id="aui-header">
        <a class="aui-btn aui-pull-left" tapmode="" onclick="history.go(-1)">
          <span class="aui-iconfont aui-icon-left"></span>
        </a>
        <div class="aui-title aui-font-size-12">我的设备</div>

      </header>
      <!--内容区 不同设备信息-->
      <div class="aui-card-list-header aui-card-list-user bg-background" style="padding: 0 !important;">
        <div class="z-shebei-box1" v-for="item in deviceList">
          <!--<a href="./shebeikongzhi.html">-->
            <div class="aui-card-list-user-name">
              <div>{{item.deviceName}}</div>
              <div class="aui-font-size-14 text-light">设备状态</div>
            </div>
            <div class="z-shebei-xian">
              <p></p>
            </div>
            <div class="userinfo-header">
              <div class="aui-info">
                <div class="aui-info-item">{{item.deviceNumber}}</div>
                <div class="aui-info-item z-shebei-mar-r">
                  <img src="../../../../static/images/center_zhengchang.png" style="width:.9rem">
                  <span class="aui-margin-l-10 aui-margin-r-10">{{item.deviceState==0?'正常':''}}</span>
                </div>
              </div>
            </div>
          <!--</a>-->

        </div>
        

      </div>

    </div>
</template>

<script>
	import Axios from 'axios'
	export default {
		name: "myshebei",
		data(){
			return{
				deviceList:[],
				page:1,
				pageSize:6
			}
		},
		created(){
			// var params = new URLSearchParams();
			// 	params.append('userId',1);
			// 	params.append('pageNum',1);
			// 	params.append('pageSize',6);
			// Axios({
			// 	method:'post',
			// 	url:'http://xiwanji.91xiaokong.com/selectToDevice',
			// 	data:params
			// }).then(res=>{
			// 	console.log(res)
			// 	this.deviceList = res.data.list
			// });
			this.$api.getMydevice({
				'pageNum':this.page,
				'pageSize':this.pageSize
			}).then(res=>{
				this.deviceList = res.data.list
			})
		}
	}
</script>

<style scoped>

</style>



