
import Vue from 'vue'
import VueRouter from 'vue-router'
import Msite from '../pages/Msite/Msite'
import Saoma from '../pages/saoma/saoma'
import Profile from '../pages/Profile/Profile'
import jifentixian from '../pages/Profile/jifentixian/jifentixian'
import mytuanyou from '../pages/Profile/mytuanyou/mytuanyou'
import myshebei from '../pages/Profile/myshebei/myshebei'
import mypintuanma from '../pages/Profile/mypintuanma/mypintuanma'
import xufeijilu from '../pages/Profile/xufeijilu/xufeijilu'
import tuihuo from '../pages/Profile/tuihuo/tuihuo'
import addzhanghu from '../pages/Profile/addzhanghu/addzhanghu'
import tixianjilu from '../pages/Profile/tixianjilu/tixianjilu'
import jifenmingxi from '../pages/Profile/jifenmingxi/jifenmingxi'
import tixian from '../pages/Profile/tixian/tixian'
import productDetail from '../pages/Msite/productDetail/productDetail'
import orderfirm from '../pages/Msite/orderfirm/orderfirm'
import addressList from '../pages/Msite/addressList/addressList'
import addAdress from '../pages/Msite/addAdress/addAdress'
import reviseAddress from '../pages/Msite/reviseAddress/reviseAddress'

import numpintuan from '../pages/Profile/numpintuan/numpintuan'

import accountList from '../pages/Profile/accountList/accountList'
import devideCode from '../pages/deviceCode/deviceCode'
import reviseAccount from '../pages/Profile/reviseAccount/reviseAccount'


Vue.use(VueRouter)
export default new VueRouter({

  routes: [
    //指的是直接跳转到指定的路径中
    {
      path: '/',
      redirect: '/msite',
      meta:{
        showFooter:true,
      }
    },
    {
      path: '/msite',
      component: Msite,
      meta:{
        showFooter:true,
      }
    },
    {
      path: '/saoma',
      component: Saoma,
      meta:{
        showFooter:true,
      }
    },
    {
      path: '/profile',
      component: Profile,
      meta:{
        showFooter:true,
      },

    },

    {
      path: '/jifentixian',
      component: jifentixian,

    },
    {
      path: '/mytuanyou',
      component: mytuanyou
    },
    {
      path: '/mypintuanma',
      component: mypintuanma
    },
    {
      path: '/myshebei',
      component: myshebei
    },
    {
      path: '/tuihuo',
      component: tuihuo
    },
    {
      path: '/xufeijilu',
      component: xufeijilu
    },
    {
      path: '/addzhanghu',
      component: addzhanghu
    },
    {
      path: '/tixianjilu',
      component: tixianjilu
    },
    {
      path: '/jifenmingxi',
      component: jifenmingxi
    },
    {
      path: '/tixian',
      component: tixian
    },
    {
      path: '/productDetail/:id',
      component: productDetail
    },
    {
      path: '/orderfirm/:id',
      component: orderfirm
    },
    {
      path: '/addressList',
      component: addressList
    },
    {
      path: '/addAdress',
      component: addAdress
    },
		{
		  path: '/reviseAddress/:address',
		  component: reviseAddress
		},

    {
      path: '/numpintuan',
      component: numpintuan
    },

		{
		  path: '/accountList',
		  component: accountList
		},
		{
			path:'/devideCode',
			component:devideCode
		},
		{
			path:'/reviseAccount/:account',
			component:reviseAccount
		}



  ]
})



