<template>
    <div class="tixian">
      <!--头部-->
      <!-- <header class="aui-bar aui-bar-nav" id="aui-header">
        <a class="aui-btn aui-pull-left" tapmode="" @click="$router.go(-1)">
          <span class="aui-iconfont aui-icon-left"></span>
        </a>
        <div class="aui-title aui-font-size-12">提现</div>
      </header> -->
			<Header title="提现">
				<a class="aui-btn aui-pull-left" tapmode="" @click="$router.go(-1)" slot="left">
				  <span class="aui-iconfont aui-icon-left"></span>
				</a>
			</Header>
      <!--内容区-->
      <ul class="aui-list aui-list-in z-margin-t5 z-background-image">
        <li class="aui-list-item">

          <div class="aui-list-item-inner aui-list-item-arrow">
            <div class="aui-list-item-title aui-font-size-12">选择收款账号</div>
          </div>
        </li>
        <li class="aui-list-item">
          <div class="aui-list-item-inner">
            <div class="aui-list-item-label" style="width: 8%;">
              ￥
            </div>
            <div class="aui-list-item-input">
              <input class="aui-font-size-12" type="number" placeholder="请输入需要提现的金额">
            </div>
          </div>
        </li>
      </ul>

      <div class="aui-card-list-content-padded aui-padded-t-5 aui-padded-b-5 z-margin-t1 aui-font-size-14">
        可以提现:100
      </div>
      <div class="aui-card-list-content-padded aui-padded-t-5 aui-padded-b-5 z-margin-t1 aui-font-size-14">
        总积分：1200
      </div>
      <div class="aui-card-list-content-padded aui-padded-t-5 aui-padded-b-5 z-margin-t1 fontcolor">
        温馨提示:最低提现金额￥100
      </div>

      <!--底部进行提交-->
      <div style="width: 100%;position: absolute;margin: auto;top: 300px;">
        <div class="aui-btn  aui-btn-block aui-btn-sm aui-margin-t-15 btn-bg z-colorhuibai">提交</div>
      </div>
    </div>
</template>

<script>
	import Header from '../../../components/HeaderTop/HeaderTop.vue'
    export default {
        name: "tixian",
				components: {
					Header
				},
    }
</script>

<style scoped>
.tixian{
	padding-top: 2.25rem;
}
</style>



