import axios from 'axios'
import qs from 'qs'

const baseURL = 'http://dishwasher.ngrok.xiaomiqiu.cn/dishwasher'
axios.defaults.baseURL = baseURL;
axios.defaults.headers.post['Content-Type'] = 'application/json;text/xml;charset=UTF-8';

const userId = window.localStorage.userId || 1;

function appendParams(played,params){
	for(let key in played){
		params.append(key,played[key])
	}
}

