<template>
  <div class="addzhanghu">
    <!--头部-->
    <header class="aui-bar aui-bar-nav" id="aui-header">
      <a class="aui-btn aui-pull-left" tapmode="" onclick="history.go(-1)">
        <span class="aui-iconfont aui-icon-left"></span>
      </a>
      <div class="aui-title aui-font-size-12">修改收款账户</div>
    </header>

    <!--内容区-->

    <ul class="aui-list aui-form-list">
      <li class="aui-list-item">

        <div class="aui-list-item-inner">
          <!--<div class="aui-list-item-title aui-font-size-12">收款方式</div>-->
          <input type="text" class="aui-font-size-12" name="accountType" placeholder="收款方式" v-model="accountType" disabled="disabled">

        </div>
      </li>

      <!-- <li class="aui-list-item">
        <div class="aui-list-item-inner">
          <div class="aui-list-item-input">
            <input type="text" class="aui-font-size-12" name="accountAccountNumber" placeholder="收款账号" v-model="accountAccountNumber">
          </div>
        </div>
      </li> -->
      <li class="aui-list-item">
        <div class="aui-list-item-inner">

          <div class="aui-list-item-input">
            <input class="aui-font-size-12" type="text" name="accountNameReal" placeholder="真实姓名" v-model="accountNameReal">
          </div>
        </div>
      </li>

      <li class="aui-list-item">
        <div class="aui-list-item-inner">

          <div class="aui-list-item-input">
            <input class="aui-font-size-12" type="number" name="accountMobile" placeholder="手机号" v-model="accountMobile">
          </div>
        </div>
      </li>


    </ul>
		<Toast v-if="show">{{message}}</Toast>

    <!--保存-->
    <div class="aui-btn  aui-btn-block aui-btn-sm z-margin-t100 btn-bg z-colorhuibai" @click="save">保存</div>

  </div>
</template>

<script>
	import Toast from '../../../components/Toast/Toast.vue'
	export default {
		name: "addzhanghu",
		data(){
			return{
				accountType:'微信',
				accountAccountNumber:null,
				accountNameReal:'',
				accountMobile:null,
				show:false,
				message:'',
				accountId:''
			}
		},
		components: {
			Toast:Toast
		},
		created(){
			let account = JSON.parse(this.$route.params.account)
			console.log(account)
			this.accountType = account.accountType
			this.accountNameReal = account.accountNameReal
			this.accountMobile = account.accountMobile
			this.accountId = account.accountId
		},
		methods:{
			save(){
				if(this.accountMobile&&this.accountNameReal){
					if(this.util.isPoneAvailable(this.accountMobile)){
						this.$api.reviseAccount({
							'accountMobile':this.accountMobile,
							'accountNameReal':this.accountNameReal,
							'accountId':this.accountId
						}).then(res=>{
							if(res.data.message == '添加成功'){
								this.$router.push({path:'/accountList'})
							}else{
								this.$toast(res.data.message)
							}
						})
					}else{
						this.$toast('手机号格式不正确')
					}
				}else{
					this.$toast('不能为空')
				}
			}
		}
	}
</script>

<style scoped>

</style>



