<template>
    <div class="jifenmingxi">
      <!--头部-->
      <!-- <header class="aui-bar aui-bar-nav" id="aui-header">
        <a class="aui-btn aui-pull-left" tapmode="" onclick="history.go(-1)">
          <span class="aui-iconfont aui-icon-left"></span>
        </a>
        <div class="aui-title aui-font-size-12">积分明细</div>
      </header> -->
	  <Header title="积分明细">
	  	<a class="aui-btn aui-pull-left" tapmode="" onclick="history.go(-1)" slot="left">
	  	  <span class="aui-iconfont aui-icon-left"></span>
	  	</a>
	  </Header>

      <!--收支明细流水-->
      <ul class="aui-list aui-media-list">

          <li class="aui-list-item" v-for="item in scoreItem">
            <div class="aui-media-list-item-inner">
              <div class="aui-list-item-inner">
                <div class="aui-list-item-title aui-font-size-18">
                  <span class="aui-pull-left aui-font-size-12">{{item.scoreItemType}}</span>
                  <span class="aui-pull-right colorOrage aui-font-size-12">￥{{item.scoreItemScore}}</span>
                </div>

              </div>
            </div>
            <div class="aui-info" style="margin-top: -14px;">
              <div class="aui-info-item aui-font-size-12">
                <span class="aui-margin-l-5">时间:{{item.scoreItemDate}}</span>
              </div>
              <!--进行选择-->
              <div class="aui-info-item aui-font-size-12 aui-pull-right">收益</div>

              <!--<div class="aui-info-item aui-font-size-12 aui-pull-right">支出</div>-->
            </div>

          </li>
        



      </ul>

    </div>
</template>

<script>
	import Header from '../../../components/HeaderTop/HeaderTop.vue'
    export default {
        name: "jifenmingxi",
				data(){
					return{
						scoreItem:[]
					}
				},
				components: {
					Header
				},
				created(){
					this.$api.getPointDetail().then(res=>{
						this.scoreItem = res.data
					})
				}
    }
</script>

<style scoped>
.jifenmingxi{
	padding-top:2.25rem ;
}
</style>



