<template>
    <div class="Profile">

      <!--头部区域-->
      <HeaderTop title="个人中心">
        <a class="aui-btn aui-pull-left"slot="left" tapmode="" onclick="history.go(-1)">
          <span class="aui-iconfont aui-icon-left"></span>
        </a>
      </HeaderTop>

      <!--头像图片-->
      <div class="aui-list-item aui-list-item-middle"
           style="background-image: url('../../../static/images/mcbg.png');background-size: cover;height: 160px;position: relative;">
        <div class="aui-media-list-item-inner z-center-flex-col">
          <div class="z-center-div1">
            <img :src="'http://'+user.userWxImg" class="aui-img-round">
          </div>
          <p>{{user.userWxName}}</p>
          <p>当前积分 {{user.userCurrentScore}}</p>
        </div>
      </div>

      <!--各项内容-->
      <ul class="aui-list aui-list-in z-background-image">

          <li class="aui-list-item">
            <div class="aui-list-item-label-icon">
              <img class="center_width_img"  src="../../../static/images/center_jifenduihuan.png">
            </div>
            <div class="aui-list-item-inner aui-list-item-arrow" @click="goto('/msite')">
              <div class="aui-list-item-title aui-font-size-12">{{msg0}}</div>
              <div class="aui-list-item-right"></div>
            </div>
          </li>


        <!--积分提现-->

          <li class="aui-list-item">
            <div class="aui-list-item-label-icon">
              <img class="center_width_img"  src="../../../static/images/center_jifentixian.png">
            </div>

            <div class="aui-list-item-inner aui-list-item-arrow" @click="go1">

                <div class="aui-list-item-title aui-font-size-12">{{msg1}}</div>
                <div class="aui-list-item-right"></div>

            </div>
          </li>
        <!--我的拼团码-->
        <li class="aui-list-item z-background-image">
          <div class="aui-list-item-label-icon">
            <img class="center_width_img"  src="../../../static/images/center_jifentixian.png">
          </div>

          <div class="aui-list-item-inner aui-list-item-arrow" @click="go2">
            <div class="aui-list-item-title aui-font-size-12">{{msg2}}</div>
            <div class="aui-list-item-right"></div>
          </div>
        </li>

      </ul>
      <ul class="aui-list aui-list-in z-background-image z-margin-t1">
          <li class="aui-list-item">
            <div class="aui-list-item-label-icon">
              <img class="center_width_img"  src="../../../static/images/center_team.png">
            </div>
            <div class="aui-list-item-inner aui-list-item-arrow" @click="go3">
              <div class="aui-list-item-title aui-font-size-12">{{msg3}}</div>
              <div class="aui-list-item-right"></div>
            </div>
          </li>
        <!--我的设备-->
          <li class="aui-list-item">
            <div class="aui-list-item-label-icon">
              <img class="center_width_img"  src="../../../static/images/center_shebei.png">
            </div>
            <div class="aui-list-item-inner aui-list-item-arrow"@click="go4">
              <div class="aui-list-item-title aui-font-size-12">{{msg4}}</div>
              <div class="aui-list-item-right"></div>
            </div>
          </li>

        <!--续费记录-->
          <li class="aui-list-item">
            <div class="aui-list-item-label-icon">
              <img class="center_width_img"  src="../../../static/images/center_shebei.png">
            </div>
            <div class="aui-list-item-inner aui-list-item-arrow"@click="go5">
              <div class="aui-list-item-title aui-font-size-12">{{msg5}}</div>
              <div class="aui-list-item-right"></div>
            </div>
          </li>

        <!--我要退货-->
          <li class="aui-list-item z-background-image">
            <div class="aui-list-item-label-icon">
              <img class="center_width_img"  src="../../../static/images/center_tuihuo.png">
            </div>
            <div class="aui-list-item-inner aui-list-item-arrow" @click="go6">
              <div class="aui-list-item-title aui-font-size-12">{{msg6}}</div>
              <div class="aui-list-item-right"></div>
            </div>
          </li>

      </ul>

    </div>

</template>

<script>
  import HeaderTop from '../../components/HeaderTop/HeaderTop';
  // import jifentixian from './jifentixian/jifentixian';
    export default {
        name: "Profile",
      data () {
        return {
         msg0: '积分兑换',
         msg1: '积分提现',
         msg2: '我的拼团码',
         msg3: '我的团友',
         msg4: '我的设备',
         msg5: '续费记录',
         msg6: '我要退货',
				 user:{}
        }
      },
			created(){
				this.$api.getMyDetail().then(res=>{
					console.log(res)
					this.user = res.data.user
				})
			},

      methods:{
        goto (path) {
          this.$router.replace(path)
        },
        go1(){

           this.$router.push('/jifentixian')
        },
        go2(){

          this.$router.push('/mypintuanma')
        },
        go3(){

          this.$router.push('/mytuanyou')
        },
        go4(){

          this.$router.push('/myshebei')
        },
        go5(){

          this.$router.push('/xufeijilu')
        },
        go6(){

          this.$router.push('/tuihuo')
        },

      },

        components:{
          //使用组件化开发
          HeaderTop,
          // jifentixian,
        },
    }
</script>

<style scoped>
  /*头部导航条*/
  .aui-bar-nav {
    background: #fd5a41;
  }
  .aui-list-item .active{
    color: #fd5a41;
  }
	.Profile{
		padding-top: 2.2rem;
	}

</style>



