
<template>
  <div class="mypintuanma">
		<Header title="我的设备">
			<a class="aui-btn aui-pull-left" tapmode="" onclick="history.go(-1)" slot="left">
			  <span class="aui-iconfont aui-icon-left"></span>
			</a>
		</Header>
    <!--内容区 不同拼团码信息-->
    <div class="aui-card-list-header aui-card-list-user bg-background">

      <div class="z-shebei-box1 z-pintuan-b">
          <div class="aui-font-size-14 text-light">设备号:{{devices.stockDetailDeviceNumber}}</div>
		  <div class="aui-font-size-14 text-light">设备名:{{devices.deviceStyle?devices.deviceStyle.devieStyleName:''}}</div>
		  <div class="aui-font-size-14 text-light">设备号:{{devices.stockDetailDeviceNumber}}</div>
		  <div class="aui-font-size-14 text-light">设备月供给:{{devices.deviceStyle?devices.deviceStyle.devieStyleMonthSupply:''}}</div>
          <div class="aui-font-size-14 text-light">设备数量:{{devices.deviceStyle?devices.deviceStyle.deviceStyleNumber:''}}</div>
      </div>
    </div>
  </div>
</template>

<script>
	import Header from '../../components/HeaderTop/HeaderTop.vue'
  export default {
    name: "deviceCode",
		data(){
			return{
				devices:{},
				
			}
		},
		components: {
			Header
		},
		created(){
			alert(JSON.stringify(this.$route.query))
			console.log(location,this.$route.query)
			this.$api.getDevice({stockDetailDeviceNumber:this.util.getQueryString('stockDetailDeviceNumber'),stockDetailId:this.util.getQueryString('stockDetailId')}).then(res=>{
				this.devices = res.data
			})
		}
  }
</script>

<style scoped>
	.mypintuanma{
		padding-top: 2.5rem;
	}
  .z-pintuan-b{border-bottom: 1px solid #fff!important;}

</style>









