<template>
    <div class="Saoma z-bg-hui">
      <!--头部-->
      <header class="aui-bar aui-bar-nav z-bar-sao-nav" id="aui-header">
        <a class="aui-btn aui-pull-left" tapmode="" onclick="history.go(-1)">
          <span class="aui-iconfont aui-icon-left"></span>
        </a>
        <div class="aui-title aui-font-size-12" @click="scanQRCode()">扫码</div>
        <a class="aui-btn aui-pull-right">
          <span class="aui-iconfont aui-icon-camera"></span>
        </a>
      </header>
      <!--扫码区域-->
      <div class="z-sao-box z-margin-t3">
        <p class="z-sao-p"></p>
      </div>

      <div class="z-sao-box z-margin-t70" @click="scanQRCode()">
        <img class="z-sao-img" src="../../../static/images/icon_saomaanniu.png">
      </div>

        <div class="z-sao-box z-input-p">
          <input class="z-sao-inpt" style="" type="text" placeholder="请输入机器码">
        </div>
    </div>
</template>

<script>
	import wx from 'weixin-jsapi'
	
    export default {
        name: "Saoma",
				data(){
					return{
						
					}
				},
				created(){
					
				},
				mounted(){
					
				},
				methods: {
					scanQRCode(){
							wx.scanQRCode({
									needResult : 1, // 默认为0，扫描结果由微信处理，1则直接返回扫描结果，
									scanType : [ "qrCode", "barCode" ], // 可以指定扫二维码还是一维码，默认二者都有
									success : function(res) {
										alert(JSON.stringify(res))
										console.log(res)
											var result = res.resultStr; // 当needResult 为 1 时，扫码返回的结果
														// code 在这里面写上扫描二维码之后需要做的内容
											window.location.href=res.resultStr
									}
						
	                   
						})
						wx.error(function(res){
							alert('err', JSON.stringify(res))
						});
					}
				},
    }
</script>

<style scoped>
  /*头部导航条*/
  .aui-bar-nav {
    background: #000;
  }
  .z-bg-hui{background: #4b4b4b!important;}
  .z-input-p{margin: 10px auto;padding-bottom: 200px;}

</style>



