<template>
  <div class="addAdress">
    <!--头部-->
    <!-- <header class="aui-bar aui-bar-nav" id="aui-header">
      <a class="aui-btn aui-pull-left" tapmode="" onclick="history.go(-1)">
        <span class="aui-iconfont aui-icon-left"></span>
      </a>
      <div class="aui-title aui-font-size-12">添加地址</div>
    </header> -->
		<Header title="添加地址">
			<a class="aui-btn aui-pull-left" tapmode="" onclick="history.go(-1)" slot="left">
			  <span class="aui-iconfont aui-icon-left"></span>
			</a>
		</Header>
    <!--内容区-->
    <ul class="aui-list aui-form-list" style="">

      <li class="aui-list-item">
        <div class="aui-list-item-inner">
          <div class="aui-list-item-input">
            <input type="text" class="aui-font-size-12" placeholder="请输入你的姓名" v-model="name">
          </div>
        </div>
      </li>
      <li class="aui-list-item">
        <div class="aui-list-item-inner">

          <div class="aui-list-item-input">
            <input type="number" class="aui-font-size-12" placeholder="请输入手机号" v-model="phone">
          </div>
        </div>
      </li>
      <!--后期做地区弹框的效果-->
      <!--<li class="aui-list-item">-->

      <!--<div class="aui-list-item-inner aui-list-item-arrow">-->
      <!--<div class="aui-list-item-title aui-font-size-12">所在地区</div>-->
      <!--</div>-->
      <!--</li>-->
      <li class="aui-list-item" style="border-bottom: 0.5px solid #f8f8f8;">
        <div class="aui-list-item-inner">

          <div class="aui-list-item-input">
            <input type="text" class="aui-font-size-12" placeholder="请输入详细地址" v-model="adress">
          </div>
        </div>
      </li>

      <li class="aui-list-item">
        <div class="aui-list-item-inner">
          <div class="aui-list-item-label aui-font-size-12">
            设为默认
          </div>
          <div class="aui-list-item-input">
            <input type="checkbox" class="aui-switch z-switch" style="margin-left: 190px;" v-model="defaultAdress">
          </div>
        </div>
      </li>
      <!--<li class="aui-list-item">-->
      <!--<div class="aui-list-item-inner">-->
      <!--<div class="aui-list-item-label aui-font-size-12">-->
      <!--设为默认地址-->
      <!--</div>-->
      <!--<div class="aui-list-item-input">-->
      <!--<input type="checkbox" class="aui-switch z-switch" v-model="addressDefaultState" checked="" style="margin-left: 190px;">-->
      <!--</div>-->
      <!--</div>-->
      <!--</li>-->

    </ul>

    <div class="aui-btn  aui-btn-block aui-btn-sm aui-margin-t-15 btn-bg colorWhite" @click="save">保存</div>

  </div>
</template>

<script>
	import Header from '../../../components/HeaderTop/HeaderTop.vue'
    export default {
        name: "addAdress",
				data(){
					return{
						name:'',
						adress:'',
						phone:null,
						defaultAdress:false,
						adressDetail:{},
						addressId:null
					}
				},
				components: {
					Header
				},
				
				methods: {
					save(){
						
						this.$api.addAddress({
							'addressName':this.name,
							'addressPhone':this.phone,
							'addressDefaultState':this.defaultAdress?0:1,
							'addressAddress':this.adress
						}).then(res=>{
							if(res.data.message=="添加地址成功"){
								this.$router.push({path:'/addressList'})
							}else{
								console.log('添加地址失败')
							}
						})
					},
					
				},
    }
</script>

<style scoped>
.addAdress{
	padding-top: 2.25rem;
}
</style>



