
<template>
  <div class="mypintuanma">
    <!--头部-->
    <!-- <header class="aui-bar aui-bar-nav" id="aui-header">
      <a class="aui-btn aui-pull-left" tapmode="" onclick="history.go(-1)">
        <span class="aui-iconfont aui-icon-left"></span>
      </a>
      <div class="aui-title aui-font-size-12">我的拼团码</div>

    </header> -->
		<Header title="我的拼团码">
			<a class="aui-btn aui-pull-left" tapmode="" onclick="history.go(-1)" slot="left">
			  <span class="aui-iconfont aui-icon-left"></span>
			</a>
		</Header>
    <!--内容区 不同拼团码信息-->
    <div class="aui-card-list-header aui-card-list-user bg-background">

      <div class="z-shebei-box1 z-pintuan-b" v-for="item in devices">
          <div class="aui-font-size-14 text-light">{{item.name}}</div>
          <div class="aui-card-list-user-name">
            <img :src="'data:image/gif;base64,'+item.url" alt="" style="margin: auto;">
          </div>

      </div>
      
    </div>
  </div>
</template>

<script>
	import Axios from 'axios'
	import Header from '../../../components/HeaderTop/HeaderTop.vue'
  export default {
    name: "mypintuanma",
		data(){
			return{
				devices:[]
			}
		},
		components: {
			Header
		},
		created(){
			this.$api.getMyQRCode().then(res=>{
				this.devices = res.data
			})
		}
  }
</script>

<style scoped>
	.mypintuanma{
		padding-top: 2.5rem;
	}
  .z-pintuan-b{border-bottom: 1px solid #fff!important;}

</style>








