<template>
    <div class="numpintuan">
      <Header title="拼团果">
        <a class="aui-btn aui-pull-left" tapmode="" @click="prev" slot="left">
          <span class="aui-iconfont aui-icon-left"></span>
        </a>
      </Header>

      <div class="aui-card-list z-margin-b0 z-border-b1" style="margin-top: 50px;background: #f5f5f5;border: none;">
        <div class="userinfo-header x-num-box">

          <div class="aui-info-item z-margin-l5">
            <span>设备类型：</span>
            <span class="aui-margin-l-10 aui-margin-r-10">{{ptResult.deviceStyleId}}</span>
          </div>
            <div class="aui-info-item z-margin-l5">
              <span>设备名称：</span>
              <span class="aui-margin-l-10 aui-margin-r-10">{{ptResult.devieStyleName}}</span>
            </div>

            <div class="aui-info-item z-margin-l5">
              <span>月供期限：</span>
              <span class="aui-margin-l-10 aui-margin-r-10">{{ptResult.deviceStyleNumber}}</span>
            </div>
          <div class="aui-info-item z-margin-l5">
            <span>月供积分</span>
            <span class="aui-margin-l-10 aui-margin-r-10">{{ptResult.devieStyleMonthSupply}}</span>
          </div>
        </div>

      </div>
    </div>
</template>

<script>
    import Header from '../../../components/HeaderTop/HeaderTop.vue'
		
    export default {
      name: "numpintuan",
      data(){
        return{
          ptResult:{},
          deviceStyleId:'',
          pid:'',

        }
      },
      components:{
        Header,
      },
      created(){
       this.getInit();
				
      },
      methods:{
        prev(){
          this.$router.go(-1);
        },

        getInit(){
					
					this.deviceStyleId = this.util.getQueryString('deviceStyleId')||1
					this.pid = this.util.getQueryString('pid')||1
          console.log(this.ptResult);
          this.$api.getPTResult(
            { 'deviceStyleId':this.deviceStyleId,
              'pid':this.pid

            }
           ).then(res=>{
           this.ptResult =res.data.deviceStyle
          })
        }



      }
    }
</script>

<style scoped>
  .x-num-box{
    font-size: 14px;margin-left: 5px;margin-right: 5px;border-radius: 5px;background: #fff;line-height: 30px;margin-bottom: 10px;
  }

</style>



