
function getQueryString(name) { 
	console.log(name)
	var reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)", "i"); 
	console.log(window.location.href.split('?'))
	var r = window.location.href.split('?').length>1?window.location.href.split('?')[1].match(reg):null; 
	if (r != null) return unescape(r[2]); 
	return null; 
} 
// 判断是否为手机号
function isPoneAvailable(pone) {
	var myreg = /^[1][3,4,5,7,8][0-9]{9}$/;
	if (!myreg.test(pone)) {
	  return false;
	} else {
	  return true;
	}
}
export default{
	isPoneAvailable,
	getQueryString
}










