<template>
  <div class="FooterGuide">
    <footer class="footer_guide">
      <span class="guide_item" @click="goto('/msite')" :class="{on:isCurrent('/msite')}">
        <span class="item_icon">
          <span class="iconfont" style="font-size: 21px;">&#xe65f;</span>
        </span>
        <span class="">首页</span>
      </span>
      <span class="guide_item" @click="goto('/saoma')" :class="{on:isCurrent('/saoma')}">
        <span class="item_icon">
          <span class="iconfont" style="font-size: 21px;">&#xe712;</span>
        </span>
        <span class="colorBlack">扫码</span>
      </span>

      <span class="guide_item" @click="goto('/profile')" :class="{on:isCurrent('/profile')}">
        <span class="item_icon">
        <span class="iconfont" style="font-size: 21px;">&#xe651;</span>
        </span>
        <span class="">我的</span>
      </span>
    </footer>
  </div>
</template>

<script>

  export default {
    name: "FooterGuide",
    methods:{
      goto(path) {
        this.$router.replace(path)
      },
      isCurrent(path) {

        return this.$route.path ==path

      },
    }
  }
</script>

<style scoped>
  .footer_guide {
    position: fixed;
    z-index: 100;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #fff;
    width: 100%;
    height: 60px !important;
    display: flex;
  }

  .footer_guide .guide_item {
    display: flex;
    flex: 1;
    text-align: center;
    flex-direction: column;
    align-items: center;
    margin: 5px;
    color: #999999;
    touch-action: none;
  }

  .footer_guide .on {
    color: #fd5a41;
  }
  .footer_guide .on span{
    font-size: 14px;
    /*margin-top :2px;*/
    /*margin-bottom: 2px;*/
  }
  .footer_guide .on span .iconfont{font-size :16px;}

</style>



