<template>
	<div v-if="show" class="toast">
		<slot></slot>
	</div>
</template>

<script>
	export default{
		name:'toast',
		data(){
			return{
				show:true
			}
		},
		mounted(){
			let that = this;
			setTimeout(function(){
				that.show = false
			},2000)
		}
	}
</script>

<style>
	.toast{
		position: fixed;
		top: 50%;
		left: 50%;
		transform: translate(-50%,-50%);
		padding: 10px 15px;
		background: rgba(0,0,0,0.5);
		border-radius: 3px;
		color: #fff;
	}
</style>



